import type {FC} from 'react'
import type React from 'react'
import {ThemeProvider} from '@primer/react'
import {AnalyticsProvider} from '@github-ui/analytics-provider'
import {BaseUrlContextProvider} from '../../activity/contexts/BaseUrlContext'

type AppProps = {
  children?: React.ReactNode
}

const appName = 'stafftools-activity-view'
const category = ''
const metadata = {}

export const App: FC<AppProps> = ({children}) => {
  /*
    hardcoded to light theme for stafftools, as it is opted out of color modes
    https://github.com/github/github/blob/17f169a6659c0df85d6557d5941413cd8145b4b6/app/controllers/stafftools_controller.rb#L22
  */
  return (
    <ThemeProvider colorMode="day">
      <div data-color-mode="light">
        <BaseUrlContextProvider baseUrl="/stafftools/repositories">
          <AnalyticsProvider appName={appName} category={category} metadata={metadata}>
            {children}
          </AnalyticsProvider>
        </BaseUrlContextProvider>
      </div>
    </ThemeProvider>
  )
}

try{ App.displayName ||= 'App' } catch {}