import {jsonRoute} from '@github-ui/react-core/json-route'
import {App} from './App'
import ActivityIndex from '../../activity/pages/ActivityIndex'
import {registerReactAppFactory} from '@github-ui/react-core/register-app'

registerReactAppFactory('stafftools-activity', () => ({
  App,
  routes: [jsonRoute({path: '/stafftools/repositories/:owner/:repo/activity', Component: ActivityIndex})],
}))
